/* Googlefont Poppins CDN Link */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

.sidebar {
    position: fixed;
    height: 100%;
    width: 240px;
    background: #243010;
    transition: all 0.5s ease;

}

.sidebar.active {
    width: 60px;
}

.sidebar .logo-details {
    height: 80px;
    display: flex;
    align-items: center;
}

.sidebar .logo-details i {
    font-size: 28px;
    font-weight: 500;
    color: #243010;
    min-width: 60px;
    text-align: center;
    cursor: pointer;
}

.sidebar .logo-details .logo_name1 {
    color: #ffffff;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;
}

.sidebar .logo-details .logo_name {
    color: #058E3F;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;
}

.logoutbtnDashDiv {
    padding: 40px;
}


.logoutbtnDash {
    display: inline-block;
    margin-top: 1rem;
    padding: .8rem 3rem;
    background: #181414;
    color: white;
    cursor: pointer;
    font-size: 1.7rem;
    border-radius: .5rem;
    font-weight: 500;
    text-align: center;
}

.logoutbtnDash:hover {
    box-shadow: 4px 4px 8px 4px rgba(0, 0, 0, 0.2);
    background: #058E3F;
    color: white;
}


.sidebar .nav-links {
    margin-top: 10px;
}

.sidebar .nav-links li {
    position: relative;
    list-style: none;
    height: 50px;
}

.sidebar .nav-links li a {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    text-decoration: none;
    transition: all 0.4s ease;
}

.sidebar .nav-links li.active {
    background: white;
    color: #058E3F;
}


.sidebar .nav-links li>.dashlinks:hover {
    box-shadow: 4px 4px 8px 4px rgb(16, 73, 17);
    border-radius: 7px;
    padding: 10px 10px;
    background-color: #058E3F;
    color: white !important;
}


/**/
select {
    font-family: Arial, sans-serif;
    font-size: 16px;
    padding: 8px 12px;
    border: 1px solid #07600b;
    border-radius: 4px;
    background-color: #dfc89e;
    color: #333;
    outline: none;
    /* Add any additional styles you want */
}

/* Style the dropdown arrow */
select::-ms-expand {
    display: none;
    /* Hide the default arrow in IE 10 and IE 11 */
}

select::after {
    content: '\25BC';
    /* Unicode character for the down arrow */
    position: absolute;
    top: 50%;
    right: 12px;
    transform: translateY(-50%);
    pointer-events: none;
    color: #555;
    /* Color of the arrow */
}

/* Style the options */
select option {
    background-color: #f1f1f1;
    /* Background color of the options */
    color: #333;
    /* Text color of the options */
}

/* Style the selected option */
select option:checked {
    background-color: #555;
    /* Background color of the selected option */
    color: #fff;
    /* Text color of the selected option */
}

/**/
.sidebar .nav-links li i {
    min-width: 60px;
    text-align: center;
    font-size: 18px;
}

.sidebar .nav-links li i:hover {
    color: #ffff;
}

.dashlinks {
    color: #058E3F;
    font-size: 15px;
}

.dashlinks i {
    color: #000000;
}




.sidebar .nav-links li.allLinks_name {
    color: #058E3F;
    font-size: 15px;
    font-weight: 400;
    white-space: nowrap;
}

.sidebar .nav-links .log_out {
    position: relative;
    bottom: -20px;
    left: 60px;
    width: 100%;
}

.total-cars-box {
    background-color: #a7d3a6;
    box-shadow: 0 4px 8px 0 white;
    border: 1px solid white;
    border-radius: 7px;
    padding: 20px 40px;
    font-size: 18px;
    margin-top: 10%;
    text-align: center;
}

.home-section {
    position: relative;
    background: #f1f1f1;
    min-height: 100vh;
    width: calc(100% - 240px);
    left: 240px;
    transition: all 0.5s ease;
}

.sidebar.active~.home-section {
    width: calc(100% - 60px);
    left: 60px;
}

.home-section nav {
    display: flex;
    justify-content: space-between;
    height: 80px;
    background: #243010;
    display: flex;
    align-items: center;
    position: fixed;
    width: calc(100% - 240px);
    left: 240px;
    z-index: 100;
    padding: 0 20px;
    transition: all 0.5s ease;
    color: #ffffff;
    top: 0px;
}

.sidebar.active~.home-section nav {
    left: 60px;
    width: calc(100% - 60px);
}

.home-section nav .sidebar-button {
    display: flex;
    align-items: center;
    position: relative;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;
}

nav .sidebar-button i {
    font-size: 35px;
    margin-right: 10px;
}

.home-section nav .search-box {
    position: relative;
    height: 50px;
    max-width: 550px;
    width: 100%;
    margin: 0 20px;
}

nav .search-box input {
    height: 100%;
    width: 100%;
    outline: none;
    background: #F5F6FA;
    border: 2px solid #EFEEF1;
    border-radius: 6px;
    font-size: 18px;
    padding: 0 15px;
}

nav .search-box .bx-search {
    position: absolute;
    height: 40px;
    width: 40px;
    background: #2697FF;
    right: 5px;
    top: 50%;
    transform: translateY(-50%);
    border-radius: 4px;
    line-height: 40px;
    text-align: center;
    color: #ffff;
    font-size: 22px;
    transition: all 0.4 ease;
}

.home-section nav .profile-details {
    display: flex;
    align-items: center;
    background: white;
    border: 2px solid #058E3F;
    border-radius: 6px;
    height: 50px;
    min-width: 100px;
    padding: 0 2px 0 2px;
    cursor: pointer;
}

nav .profile-details .admin_name {
    font-size: 15px;
    font-weight: 500;
    color: #058E3F;
    margin: 20px 20px 20px 20px;
    white-space: nowrap;
}

.home-section .home-content {
    position: relative;
    padding-top: 104px;
}

.home-content .overview-boxes {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0 20px;
    margin-bottom: 26px;
}

.addcarform label {
    float: left;
    color: #1a1a1a;
    font-size: 15px;
    border: 1px;
    padding: 5;
    margin: 8px 0px;
    width: 80px;
}


.addcarform input {
    border: 1px solid #1a1a1a;
    border-radius: 3px;
    padding: 2px;
    margin: 8px 0px;
    width: 200px;
}

.salecartableDiv {
    background-color: #a7d3a6;
    box-shadow: 0 4px 8px 0 white;
    border: 1px solid white;
    border-radius: 7px;
    padding: 20px 40px;
    font-size: 18px;
    margin-top: 10%;
    text-align: center;
}

.salecartable {
    border-collapse: collapse;
    width: 100%;
    color: #1a1a1a;
}

.salecartable th {
    border: 1px solid #1a1a1a;
    padding: 8px;
}


.salecartable td {
    border: 1px solid #1a1a1a;
    padding: 8px;
}

.overview-boxes .box {
    display: flex;
    align-items: center;
    justify-content: center;
    width: calc(100% / 4 - 15px);
    background: #fff;
    padding: 15px 14px;
    border-radius: 12px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
}

.overview-boxes .box-topic {
    font-size: 20px;
    font-weight: 500;
}

.home-content .box .number {
    display: inline-block;
    font-size: 35px;
    margin-top: -6px;
    font-weight: 500;
}

.home-content .box .indicator {
    display: flex;
    align-items: center;
}

.home-content .box .indicator i {
    height: 20px;
    width: 20px;
    background: #8FDACB;
    line-height: 20px;
    text-align: center;
    border-radius: 50%;
    color: #fff;
    font-size: 20px;
    margin-right: 5px;
}

.box .indicator i.down {
    background: #e87d88;
}

.home-content .box .indicator .text {
    font-size: 12px;
}

.home-content .box .cart {
    display: inline-block;
    font-size: 32px;
    height: 50px;
    width: 50px;
    background: #cce5ff;
    line-height: 50px;
    text-align: center;
    color: #66b0ff;
    border-radius: 12px;
    margin: -15px 0 0 6px;
}

.home-content .box .cart.two {
    color: #058E3F;
    background: #C0F2D8;
}

.home-content .box .cart.three {
    color: #ffc233;
    background: #ffe8b3;
}

.home-content .box .cart.four {
    color: #248999;
    background: #d4eff7;
}

.home-content .total-order {
    font-size: 20px;
    font-weight: 500;
}

.home-content .sales-boxes {
    display: flex;
    justify-content: space-between;
}

/* left box */
.home-content .sales-boxes .recent-sales {
    width: 65%;
    background: #a7d3a6;
    padding: 20px 30px;
    margin: 0 20px;
    border-radius: 12px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
}

.home-content .sales-boxes .sales-details {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.sales-boxes .box .title {
    font-size: 24px;
    font-weight: 500;
    color: #058e3f;
    /* margin-bottom: 10px; */
}

.sales-boxes .sales-details li.topic {
    font-size: 20px;
    font-weight: 500;
}

.sales-boxes .sales-details li {
    list-style: none;
    margin: 8px 0;
}

.sales-boxes .sales-details li a {
    font-size: 18px;
    color: #333;
    font-size: 400;
    text-decoration: none;
}

.sales-boxes .box .button {
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

.sales-boxes .box .button a {
    color: #fff;
    background: #0A2558;
    padding: 4px 12px;
    font-size: 15px;
    font-weight: 400;
    border-radius: 4px;
    text-decoration: none;
    transition: all 0.3s ease;
}

.sales-boxes .box .button a:hover {
    background: #0d3073;
}

/* Right box */
.home-content .sales-boxes .top-sales {
    width: 35%;
    background: #fff;
    padding: 20px 30px;
    margin: 0 20px 0 0;
    border-radius: 12px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
}

.sales-boxes .top-sales li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 10px 0;
}

.sales-boxes .top-sales li a img {
    height: 40px;
    width: 40px;
    object-fit: cover;
    border-radius: 12px;
    margin-right: 10px;
    background: #333;
}

.sales-boxes .top-sales li a {
    display: flex;
    align-items: center;
    text-decoration: none;
}

.sales-boxes .top-sales li .product,
.price {
    font-size: 17px;
    font-weight: 400;
    color: #333;
}

/* Responsive Media Query */
@media (max-width: 1240px) {
    .sidebar {
        width: 60px;
    }

    .sidebar.active {
        width: 220px;
    }

    .home-section {
        width: calc(100% - 60px);
        left: 60px;
    }

    .sidebar.active~.home-section {
        /* width: calc(100% - 220px); */
        overflow: hidden;
        left: 220px;
    }

    .home-section nav {
        width: calc(100% - 60px);
        left: 60px;
    }

    .sidebar.active~.home-section nav {
        width: calc(100% - 220px);
        left: 220px;
    }
}

@media (max-width: 1220px) {
    .logoutbtnDashDiv {
        padding: 0;
    }
}

@media (max-width: 1150px) {
    .home-content .sales-boxes {
        flex-direction: column;
    }

    .home-content .sales-boxes .box {
        width: 100%;
        overflow-x: scroll;
        margin-bottom: 30px;
    }

    .home-content .sales-boxes .top-sales {
        margin: 0;
    }

    .salecartable {
        width: 30%;
    }


}

@media (max-width: 1000px) {
    .overview-boxes .box {
        width: calc(100% / 2 - 15px);
        margin-bottom: 15px;
    }
}

@media (max-width: 700px) {

    nav .sidebar-button .dashboard,
    nav .profile-details .admin_name,
    nav .profile-details i {
        display: none;
    }

    .home-section nav .profile-details {
        height: 50px;
        min-width: 80px;
    }

    .home-content .sales-boxes .sales-details {
        width: 560px;
    }
}

@media (max-width: 550px) {
    .overview-boxes .box {
        width: 100%;
        margin-bottom: 15px;
    }

    .sidebar.active~.home-section nav .profile-details {
        display: none;
    }
}

.filter-container {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    justify-content: center;
}

.filter-container input {
    padding: 8px;
    border: none;
    border-radius: 4px;
    font-size: 14px;
    outline: none;
    width: 150px;
    background-color: #86cfdb;
    /* Set your preferred background color */
    color: #0c0202;
    /* Set your preferred text color */
}

.salecartableDivrq {
    background-color: #a7d3a6;
    box-shadow: 0 4px 8px 0 white;
    border: 1px solid white;
    border-radius: 7px;
    padding: 10px;
    /* Adjust the padding to make it smaller */
    font-size: 14px;
    /* Adjust the font size to make it smaller */
    margin-top: 10%;
    text-align: center;
    display: inline-block;
    margin-left: -131px;
    overflow-x: auto;
    width: fit-content;
}

.salecartableDivrq .table-container {
    display: inline-block;
    max-width: 100%;
    overflow-x: auto;
}

.salecartableDivrq .salecartable {
    width: fit-content;
    table-layout: auto;
}

.salecartableDivrq .salecartable th,
.salecartableDivrq .salecartable td {
    padding: 6px;
    /* Adjust the padding to make it smaller */
    text-align: left;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}